.app .brand {
  position: absolute;
  top: 5px;
  left: 5px;
  display: inline-block;
  background: #2d2d2d url("../Home/logo.png") no-repeat center center;
  width: 40px;
  height: 40px;
  background-size: 80%;
  margin: 0 10px 0 0;
  border-radius: 20px;
