.home dd {
  margin-bottom: 15px;
}

.masthead {
  background: #2d2d2d;
  padding: 40px 20px;
  color: white;
  text-align: center;
}

.masthead .logo {
  margin: auto;
  height: 200px;
  width: 200px;
  border-radius: 100px;
  border: 1px solid #33e0ff;
  box-shadow: inset 0 0 10px #33e0ff;
  vertical-align: middle;
}

.masthead .logo p {
  line-height: 200px;
  margin: 0px;
}

.masthead .logo img {
  width: 75%;
  margin: auto;
}

.masthead h1 {
  color: #33e0ff;
  font-size: 4em;
}

.masthead h2 {
  color: #ddd;
  font-size: 2em;
  margin: 20px;
}

.masthead a {
  color: #ddd;
}

.masthead p {
  margin: 10px;
}

.masthead .humility {
  color: #777;
}

.masthead .humility a {
  color: #777;
}

.masthead .github {
  font-size: 1.5em;
}

.counterContainer {
  text-align: center;
  margin: 20px;
}



